<template>
  <div id='app'>
    <nav class='navbar navbar-default'>
      <div class='navbar-header'>
        <!-- <button type='button' class='navbar-toggle collapsed' data-toggle='collapse' data-target='#bs-example-navbar-collapse-1' aria-expanded='false'>
          <span class='sr-only'>Toggle navigation</span>
          <span class='icon-bar'></span>
          <span class='icon-bar'></span>
          <span class='icon-bar'></span>
        </button> -->
        <!--<img src='./assets/brand.png' alt=''>-->
      </div>
      <div class='collapse navbar-collapse' id='bs-example-navbar-collapse-1'>
        <ul class='nav navbar-nav'>
          <li>
            <a href='#'>Topology</span>
            </a>
          </li>
          <li>
            <a href='#'>Dashboard</a>
          </li>
          <!-- <li>
                    <a href='#'>History</a>
                  </li> -->
        </ul>
        <ul class='nav navbar-nav navbar-right'>
          <li>
            <a href='#'>Sign Out(admin)</a>
          </li>
          <li>
            <a href='#'>Settings</a>
          </li>
          <li>
            <a href='#'>Help</a>
          </li>
        </ul>
      </div>
    </nav>
    <!-- <div id='page-frame' class='split split-vertical'>
          <div id='page-tree' class='split split-horizontal'>
            <div class='input-group'>
              <input type='text' class='form-control input-sm' placeholder='Search for...'>
              <span class='input-group-btn'>
                <button class='btn btn-default btn-sm' type='button'>
                  <i class='glyphicon glyphicon-search' aria-hidden='true'></i>
                </button>
              </span>
            </div>
            <div class='sidebar-wrapper'>
              <z-tree></z-tree>
            </div>
          </div>
          <div id='page-topology' class='split split-horizontal'>
            <div id='topology-toolbar'><input type='checkbox'>test</input>
            </div>
            <cytoscape></cytoscape>
          </div>
        </div>
        <div id='page-grid' class='split split-vertical'>
          <js-grid></js-grid>
        </div> -->
    <div id='page-frame' class='split split-vertical'>
      <!-- <div id='page-topology'> -->
      <cytoscape></cytoscape>
      <!-- </div> -->
    </div>
    <div id='page-grid' class='split split-vertical'>
      <js-grid></js-grid>
    </div>
  </div>
</template>

<script>
import 'es6-promise/auto'
import store from './vuex/store'
import { mapGetters, mapActions } from 'vuex'
import 'expose-loader?$!expose-loader?jQuery!jquery'
// import ZTree from './shared-components/ZTree'
import jsGrid from './shared-components/jsGrid'
import Cytoscape from './shared-components/cytoscape'
import EventBus from './shared-components/event-bus'
import axios from 'axios';
import { HTTP } from './http-common'
import Split from '../static/split/split.min'
import '../static/bootstrap/bootstrap.min'
import '../static/chosen/chosen.jquery'

export default {
  store,
  components: {
    // ZTree,
    jsGrid,
    Cytoscape
  },

  mounted() {
    Split(['#page-frame', '#page-grid'], {
      direction: 'vertical',
      sizes: [75, 20],
      gutterSize: 3,
      minSize: 100,
      onDrag: () => { this.onGridResize(); },
      onDragEnd: () => { this.onGridResized(); }
    })

    // Split(['#page-tree', '#page-topology'], {
    //   sizes: [15, 85],
    //   minSize: 100,
    //   gutterSize: 5
    // })

    $(window).resize(this.onGridResize);
    this.onGridResize();
    this.request();
    this.poll();
  },
  data() {
    return {
      pollTimerId: 0
    }
  },
  methods: {
    ...mapActions(['fetchElements']),
    onGridResize() {
      let position = $('#page-grid').position();
      EventBus.$emit('GRID_RESIZE', position.left, position.top);
    },
    onGridResized() {
      EventBus.$emit('GRID_RESIZED');
    },
    jsonEqual(a, b) {
      return JSON.stringify(a) === JSON.stringify(b);
    },
    request() {
      axios.all([
        HTTP.get('discoveryNodes.json'),
        HTTP.get('discoveryLinks.json'),
        HTTP.get('discoveryTunnels.json')
      ])
        .then(axios.spread((nodes, links, tunnels) => {
          this.fetch({ nodes, links, tunnels });
        }))
        .catch((reason) => {
          console.log(reason);
        });
    },
    fetch(response) {
      if (response !== undefined) {
        let elements = {
          nodes: response.nodes.data,
          links: response.links.data,
          tunnels: response.tunnels.data
        }
        if (!this.jsonEqual(elements, this.elements)) {
          this.fetchElements(elements);
        }
      }
    },
    poll() {
      this.pollTimerId = setInterval(() => {
        this.request();
      }, 3000);
    }
  },
  computed: {
    ...mapGetters({ elements: 'getElements' })
  }
}
</script>

<style>
@import '../static/bootstrap/bootstrap.min.css';
@import '../static/bootstrap/bootstrap-unresponsive.css';
@import '../static/chosen/bootstrap-chosen.css';

* {
  /*border: 1px solid red;*/
}

html,
body {
  height: 100%;
  overflow: hidden;
  font-size: 12px;
  font-family: helvetica, sans-serif;
}
.split,
.split-flex {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;

  overflow-y: hidden;
  overflow-x: hidden;
}

#app {
  height: 100%;
  overflow: hidden;
}

#page-grid {
  overflow: hidden;
}

#page-topology {
  overflow: hidden;
}

.gutter {
  background-color: #eee;
  background-repeat: no-repeat;
  background-position: 50%;
}

.gutter.gutter-horizontal {
  cursor: ew-resize;
}

.gutter.gutter-vertical {
  cursor: ns-resize;
}

.split.split-horizontal,
.gutter.gutter-horizontal {
  height: 100%;
  float: left;
}

.navbar {
  margin-bottom: 0px;
  border-bottom: 1px solid #e0e0e0;
}

img {
  padding-top: 5px;
}

.navbar-default {
  color: #9e9e9e;
  background-color: #fff;
  height: 52px;
}

.nav>li>a:hover,
.nav>li>a:focus {
  color: #9e9e9e;
  background-color: silver;
}

.navbar-default .navbar-nav>li>a {
  font-size: 14px;
  font-weight: bold;
  color: #009688;
}

.navbar-default .navbar-right>li>a {
  color: #9e9e9e;
}
</style>



