<template>
  <div id='cy'>
    <button id='topology-randomize' class="btn btn-sm btn-default">
      <span class="glyphicon glyphicon-certificate"></span>
    </button>
    <div id='topology-toolbar' class='btn-group' data-toggle='buttons'>
      <label @click='toggleEdgeFilter' class='btn btn-default active btn-sm'>
        <input type='checkbox' value='link' checked> Link
      </label>
      <label @click='toggleEdgeFilter' class='btn btn-default active btn-sm'>
        <input type='checkbox' value='tunnels' checked> Tunnel
      </label>
      <!-- <label @click='toggleEdgeFilter' class='btn btn-default deactive btn-sm' data-toggle="tab" disabled>
            <input type='checkbox' value='pw'> PW
          </label> -->
    </div>
    <div id='topology-chosen'>
      <select data-placeholder='Select link or tunnel to highlight' class='chosen-select-deselect'>
        <option value=''></option>
        <optgroup label='Links (12)'>
          <option>Dallas Cowboys</option>
          <option>New York Giants</option>
          <option>Philadelphia Eagles</option>
          <option>Washington Redskins</option>
        </optgroup>
        <optgroup label='Tunnels (20)'>
          <option>Chicago Bears</option>
          <option>Detroit Lions</option>
          <option>Green Bay Packers</option>
          <option>Minnesota Vikings</option>
        </optgroup>
        <optgroup label='Lsps (123)'>
          <option>Chicago Bears</option>
          <option>Detroit Lions</option>
          <option>Green Bay Packers</option>
          <option>Minnesota Vikings</option>
        </optgroup>
      </select>
    </div>
  </div>
</template>

<script>
import store from '../vuex/store'
import { mapGetters } from 'vuex'
import 'expose-loader?$!expose-loader?jQuery!jquery'
import cytoscape from 'cytoscape'
import panzoom from 'cytoscape-panzoom'
import autopanOnDrag from 'cytoscape-autopan-on-drag'
import contextMenus from 'cytoscape-context-menus'
import navigator from 'cytoscape-navigator'
import qtip from 'cytoscape-qtip'
import EventBus from './event-bus'
import coseBilkent from 'cytoscape-cose-bilkent'
import { HTTP } from '../http-common'

export default {
  mounted() {
    panzoom(cytoscape);
    autopanOnDrag(cytoscape);
    navigator(cytoscape);
    contextMenus(cytoscape, $);
    qtip(cytoscape);
    coseBilkent(cytoscape);

    this.init();

    store.subscribe((mutation, state) => {
      this.onUpdate(mutation.payload);
    });

    $('.chosen-select').chosen();
    $('.chosen-select-deselect').chosen({ allow_single_deselect: true });

    $('#topology-chosen').on('mousewheel', (event) => {
      if (event.originalEvent.wheelDelta > 0 || event.originalEvent.detail < 0) {
        console.log('up');
      } else {
        console.log('down');
      }

      event.preventDefault();
      event.stopPropagation();
      event.stopImmediatePropagation();
    });

    EventBus.$on('GRID_RESIZE', (width, height) => {
      $('.cytoscape-navigator').css('top', (height - 205) + 'px');
      this.cy.resize();
    });

    EventBus.$on('GRID_ITEM_SELECTED', (selectedItem) => {
      if (this.itemAnimationTimerId) {
        clearInterval(this.itemAnimationTimerId);
      }

      // if (this.edgesForPathDisplay.length > 0) {
      //   this.removeEdgesForPathDisplay();
      // }

      if (selectedItem.item.type === 'node') {
        let node = this.cy.filter(`node[id='${selectedItem.item.id}']`);
        this.itemAnimationTimerId = setInterval(() => {
          node.animate({ style: { 'border-color': '#3F89DF', 'border-width': 8, 'border-opacity': 0.2 } }, { duration: 400 })
            .animate({ style: { 'border-color': '#455A64', 'border-width': 1, 'border-opacity': 1 } }, { duration: 400 });
        }, 800);
      }

      if (selectedItem.item.type === 'link') {
        let edge = this.cy.filter(`edge[id='${selectedItem.item.id}']`);
        this.itemAnimationTimerId = setInterval(() => {
          edge.animate({ style: { 'width': 6 } }, { duration: 400 })
            .animate({ style: { 'width': 1 } }, { duration: 400 });
        }, 800);
      }

      if (selectedItem.item.id.indexOf('TUNNELTRAIL') > -1) {
        this.selectedTunnelId = selectedItem.item.id;
        this.flagLspDisplayMode = true;
        this.onUpdate(store.state.elements);
      }
    });
    // cy.on('ready', function() {
    //   updateBounds();
    // });
    // //if they resize the window, resize the diagram
    // $(window).resize(function() {
    //   updateBounds();
    // });

    // var updateBounds = function() {
    //   var bounds = cy.elements().boundingBox();
    //   $('#cyContainer').css('height', bounds.h + 300);
    //   cy.center();
    //   cy.resize();
    //   //fix the Edgehandles
    //   $('#cy').cytoscapeEdgehandles('resize');
    // };
  },
  data() {
    return {
      initLoad: false,
      showPortLabel: false,
      labelFormat: (item) => { return item.name },
      nodesPos: [],
      nodeIcons: new Map([['91', 'icon_b'], ['92', 'icon_m'], ['93', 'icon_m'], ['94', 'icon_m'], ['95', 'icon_a']]),
      itemAnimationTimerId: 0,
      selectedTunnelId: '',
      flagLspDisplayMode: false
      // edgesForPathDisplay: []
    }
  },
  methods: {
    toggleEdgeFilter(event) {
      // if (this.edgesForPathDisplay.length > 0) {
      //   return;
      // }

      let isChecked = ($(event.target).find('input:checked').length === 0);
      let target = $(event.target).find('input').val();

      this.cy.batch(() => {
        this.cy.nodes().connectedEdges('.' + target).forEach((ele, i, eles) => {
          if (isChecked) {
            ele.show();
          } else {
            ele.hide();
          }
        });
      })
    },
    // removeEdgesForPathDisplay() {
    //   if (this.edgesForPathDisplay.length > 0) {
    //     this.cy.batch(() => {
    //       this.edgesForPathDisplay.forEach((element) => {
    //         // console.log(element);
    //         // if (element.group === 'nodes' || element.classes === 'link') {
    //         //   return;
    //         // }

    //         let edge = this.cy.filter(`edge[id = "${element.data.id}"]`);
    //         if (edge.length > 0) {
    //           this.cy.remove(edge);
    //         }
    //       }, this);

    //       this.cy.nodes().connectedEdges().forEach((ele) => { ele.show(); });
    //       this.edgesForPathDisplay = [];
    //     });
    //   }
    // },
    init() {
      let cy = this.cy = cytoscape(
        {
          container: document.getElementById('cy'),
          boxSelectionEnabled: true,
          autounselectify: false,
          wheelSensitivity: 0.1,
          elements: [],
          style: [ // the stylesheet for the graph
            {
              selector: 'node',
              style: {
                // 'shape': 'rectangle',
                'border-width': 1,
                'label': 'data(label)',
                // 'background-fit': 'none',
                // 'background-image-opacity': 1,
                // 'text-valign': 'center',
                // 'text-outline-width': 2,
                // 'text-outline-color': '#888',
                'border-color': '#455A64',
                'background-color': '#859AA4'
              }
            },
            {
              selector: 'edge',
              style: {
                'width': 'data(lineWidth)',
                'line-color': '#859AA4',
                'opacity': '1',
                // 'target-arrow-color': 'data(lineColor)',
                // 'target-arrow-shape': 'triangle',
                // 'haystack-radius': '0.5',
                // 'opacity': '0.4',
                // 'line-color': '#bbb',
                // 'width': 'mapData(1, 0, 1, 1, 8)',
                // 'label': 'data(label)',
                'source-label': 'data(labelSourcePort)',
                'target-label': 'data(labelTargetPort)',
                'source-text-offset': 40,
                'target-text-offset': 40,
                // 'source-text-margin-y': -10,
                // 'target-text-margin-y': -10,
                'font-size': 10,
                'text-rotation': 'autorotate'
                // 'text-halign': 'right',
                // 'text-valign': 'top',
                // 'text-margin-y': -5
              }
            },
            {
              selector: '.link',
              style: {
                'curve-style': 'bezier',
                'control-point-step-size': 18,
                'overlay-padding': '2.5px'
                // 'target-arrow-shape': 'triangle',
                // 'target-arrow-color': '#859AA4',
                // 'arrow-scale': 0.7
              }
            },
            {
              selector: '.tunnel',
              style: {
                // 'source-endpoint': 'outside-to-node',
                // 'target-endpoint': 'outside-to-node',
                // 'source-arrow-shape': 'triangle-cross',
                // 'source-arrow-fill': 'hollow',
                // 'source-distance-from-node': '20px',
                // 'target-distance-from-node': '20px',
                'width': 20,
                'line-color': '#616161',
                'opacity': 0.25,
                'overlay-padding': '12px'
              }
            },
            {
              selector: '.tunnels',
              style: {
                // 'source-endpoint': 'outside-to-node',
                // 'target-endpoint': 'outside-to-node',
                // 'source-arrow-shape': 'triangle-cross',
                // 'source-arrow-fill': 'hollow',
                // 'source-distance-from-node': '20px',
                // 'target-distance-from-node': '20px',
                'width': 20,
                'line-color': '#616161',
                'opacity': 0.25,
                'overlay-padding': '12px'
              }
            },
            {
              selector: '.lsp_wk',
              style: {
                'width': '3',
                'curve-style': 'unbundled-bezier',
                'source-text-offset': 50,
                'target-text-offset': 50,
                'source-distance-from-node': '20px',
                'target-distance-from-node': '20px',
                'target-arrow-shape': 'triangle',
                'target-arrow-color': '#FFC107',
                'line-style': 'solid',
                'line-color': '#FFC107',
                'text-wrap': 'wrap',
                'opacity': 1
              }
            },
            {
              selector: '.lsp_pt',
              style: {
                'width': '3',
                'curve-style': 'unbundled-bezier',
                'source-text-offset': 50,
                'target-text-offset': 50,
                'source-distance-from-node': '20px',
                'target-distance-from-node': '20px',
                'target-arrow-shape': 'triangle',
                'target-arrow-color': '#512DA8',
                'line-style': 'solid',
                'line-color': '#512DA8',
                'opacity': 1
              }
            },
            {
              selector: ':selected',
              style: {
                'line-color': '#616161',
                'background-color': '#2D3B41',
                'target-arrow-color': '#616161',
                'source-arrow-color': '#616161',
                'opacity': 1
              }
            },
            {
              selector: '.bar',
              style: {
                'width': '3',
                'curve-style': 'unbundled-bezier',
                'source-endpoint': '10px 20px',
                'target-endpoint': '-10px 20px',
                'line-style': 'dashed',
                'line-color': '#0F0',
                'target-arrow-shape': 'triangle',
                'target-arrow-color': '#f00',
                'opacity': 1
              }
            },
            {
              selector: '.icon_a',
              style: {
                // 'background-image': '../../static/image/icon_a.png'
              }
            },
            {
              selector: '.icon_b',
              style: {
                // 'background-image': '../../static/image/icon_b.png'
              }
            },
            {
              selector: '.icon_m',
              style: {
                // 'background-image': '../../static/image/icon_m.png'
              }
            }
          ],
          layout: {
            name: 'cose-bilkent'
          }
        },
      )

      cy.viewport({ zoom: 1, pan: { x: 0, y: 0 } });
      cy.minZoom(0.1);
      cy.maxZoom(10);

      cy.panzoom({
        minZoom: 0.1,
        maxZoom: 10,
        animateOnFit: () => { return true },
        fitAnimationDuration: 300
      });

      cy.autopanOnDrag({});

      cy.navigator({
        container: false, // can be a HTML or jQuery element or jQuery selector
        viewLiveFramerate: 0, // set false to update graph pan only on drag end; set 0 to do it instantly; set a number (frames per second) to update not more than N times per second
        thumbnailEventFramerate: 30, // max thumbnail's updates per second triggered by graph updates
        thumbnailLiveFramerate: 2, // max thumbnail's updates per second. Set false to disable
        dblClickDelay: 200, // milliseconds
        removeCustomContainer: true, // destroy the container specified by user on plugin destroy
        rerenderDelay: 0 // ms to throttle rerender updates to the panzoom for performance
      });

      let self = this;
      cy.contextMenus({
        menuItems: [
          {
            id: 'label_format_name',
            content: 'Change label [ NAME ]',
            tooltipText: 'Change label [ NAME ]',
            // image: { src: 'remove.svg', width: 12, height: 12, x: 6, y: 4 },
            selector: 'node, edge',
            onClickFunction: (event) => {
              self.labelFormat = (item) => {
                return `${item.name}`;
              }
              self.onUpdate(store.state.elements);
            },
            coreAsWell: true
          },
          {
            id: 'label_format_ip',
            content: 'Change label [ IP ]',
            tooltipText: 'Change label [ IP ]',
            // image: { src: 'remove.svg', width: 12, height: 12, x: 6, y: 4 },
            selector: 'node, edge',
            onClickFunction: (event) => {
              self.labelFormat = (item) => {
                return `${item.id}`;
              }
              self.onUpdate(store.state.elements);
            },
            coreAsWell: true
          },
          {
            id: 'label_format_name_ip',
            content: 'Change label [ NAME (IP) ]',
            tooltipText: 'Change label [ NAME (IP) ]',
            selector: 'node, edge',
            onClickFunction: (event) => {
              self.labelFormat = (item) => {
                return `${item.name} (${item.id})`;
              }
              self.onUpdate(store.state.elements);
            },
            hasTrailingDivider: true,
            coreAsWell: true
          },
          {
            id: 'show_port_label',
            content: 'Toggle port label',
            tooltipText: 'Toggle port label',
            selector: 'node, edge',
            onClickFunction: (event) => {
              self.showPortLabel = !self.showPortLabel;
              self.onUpdate(store.state.elements);
            },
            coreAsWell: true
          }
        ]
      });

      let makeTooltip = function(type, elem) {
        let text = '';
        let data = elem.data();
        if (type === 'node') {
          text = `<b>ID</b>: ${data.id}<br><b>NAME</b>: ${data.name}<br><b>ME ID</b>: ${data.me}<br><b>PRODUCT NAME</b>: ${data.product}<br>`;
        }

        if (type === 'edge') {
          if (elem.hasClass('tunnel')) {
            text = `<b>ID</b>: ${data.id}<br><b>NAME</b>: ${data.name}<br><b>CIR</b>: ${data.cir}<br><b>PIR</b>: ${data.pir}
            <br><b>[ WORKING LSP ]</b>
            <br><b>&nbsp;&nbsp;NAME</b>: ${data.wkLspName}
            <br><b>&nbsp;&nbsp;PATH TYPE</b>: ${data.wkLspPathType}
            <br><b>&nbsp;&nbsp;SRC NODE ID / NAME</b>: ${data.wkLspSrcNodeId} / ${data.wkLspSrcNodeName}
            <br><b>&nbsp;&nbsp;SRC IN/OUT LABEL</b>: ${data.wkLspSrcInLabel} / ${data.wkLspSrcOutLabel}
            <br><b>&nbsp;&nbsp;DST NODE ID / NAME </b>: ${data.wkLspDstNodeId} / ${data.wkLspDstNodeName}
            <br><b>&nbsp;&nbsp;DST IN/OUT LABEL</b>: ${data.wkLspDstInLabel} / ${data.wkLspDstOutLabel}
            <br><b>&nbsp;&nbsp;OAM - MEG NAME</b>: ${data.wkLspOamMegName} <b>MEP ID:</b> ${data.wkLspOamMepId} <b>RMEP ID:</b> ${data.wkLspOamRMepId} <b>CC INTERVAL:</b> ${data.wkLspOamCcInterval}
            <br><b>[ PROTECTION LSP ]</b>
            <br><b>&nbsp;&nbsp;NAME</b>: ${data.ptLspName}
            <br><b>&nbsp;&nbsp;PATH TYPE</b>: ${data.ptLspPathType}
            <br><b>&nbsp;&nbsp;SRC NODE ID / NAME</b>: ${data.ptLspSrcNodeId} / ${data.ptLspSrcNodeName}
            <br><b>&nbsp;&nbsp;SRC IN/OUT LABEL</b>: ${data.ptLspSrcInLabel} / ${data.ptLspSrcOutLabel}
            <br><b>&nbsp;&nbsp;DST NODE ID / NAME </b>: ${data.ptLspDstNodeId} / ${data.ptLspDstNodeName}
            <br><b>&nbsp;&nbsp;DST IN/OUT LABEL</b>: ${data.ptLspDstInLabel} / ${data.ptLspDstOutLabel}
            <br><b>&nbsp;&nbsp;OAM - MEG NAME</b>: ${data.ptLspOamMegName} <b>MEP ID:</b> ${data.ptLspOamMepId} <b>RMEP ID:</b> ${data.ptLspOamRMepId} <b>CC INTERVAL:</b> ${data.ptLspOamCcInterval}`;
          } else if (elem.hasClass('tunnels')) {
            text = `<b>TUNNELS (${data.count})</b><br><b>${data.source} - ${data.target}</b>`;
          } else if (elem.hasClass('lsp_wk') || elem.hasClass('lsp_pt')) {
            text = `<b>ID</b>: ${data.linkId}<br><b>NAME</b>: ${data.name}<br><b>SRC NODE</b>: ${data.source}<br><b>DST NODE</b>: ${data.target}
            <br><b>SRC PORT</b>: ${data.linkSrcPortId}<br><b>DST PORT</b>: ${data.linkDstPortId}<br><b>SRC IN/OUT LABEL</b>: ${data.srcInLabel} / ${data.srcOutLabel}
            <br><b>DST IN/OUT LABEL</b>: ${data.dstInLabel} / ${data.dstOutLabel}`;
          } else {
            text = `<b>ID</b>: ${data.id}<br><b>NAME</b>: ${data.name}<br><b>SRC NODE</b>: ${data.source}<br><b>DST NODE</b>: ${data.target}
            <br><b>SRC PORT</b>: ${data.sourcePort}<br><b>DST PORT</b>: ${data.targetPort}<br><b>SRC PRODUCT NAME</b>: ${data.sourceProduct}
            <br><b>DST PRODUCT NAME</b>: ${data.targetProduct}<br><b>BANDWIDTH</b>: ${data.bandwidth}<br><b>MAX BANDWIDTH</b>: ${data.maxBandwidth}`;
          }
        }
        return text;
      }
      let showTootip = function(elem, text) {
        elem.qtip({
          content: text,
          position: {
            my: 'top center',
            at: 'bottom center'
          },
          show: {
            cyBgOnly: true
          },
          style: {
            classes: 'qtip-bootstrap'
          }
        })
      }

      cy.on('click', 'node', (event) => {
        let elem = event.target;
        elem.qtip('api').destroy();
        showTootip(elem, makeTooltip('node', elem));
      });

      cy.on('click', 'edge', (event) => {
        let elem = event.target;
        elem.qtip('api').destroy();
        showTootip(elem, makeTooltip('edge', elem));
      });

      // cy.on('boxselect', function(event) {
      //   cy.elements().forEach(function(ele, i, eles) {
      //     if (ele.selected()) {
      //       // console.log(ele.id());
      //     }
      //   });
      // });

      // cy.on('select', (event) => {
      //   this.removeEdgesForPathDisplay();
      // });

      // cy.on('cxttap', (event) => { 
      //   if (this.edgesForPathDisplay.length > 0) {
      //     this.removeEdgesForPathDisplay();
      //     event.preventDefault();
      //   }
      // });

      cy.on('mouseup', (event) => {
        // if (event.target === this.cy) {
        //   console.log(event);
        //   return;
        // }

        if (this.itemAnimationTimerId) {
          clearInterval(this.itemAnimationTimerId);
        }

        if (this.flagLspDisplayMode) {
          return;
        }

        setTimeout(() => {
          let elements = [];
          cy.elements().forEach((ele, i, eles) => {
            if (ele.selected()) {
              elements.push(ele.data());
            }
          });

          // if (this.edgesForPathDisplay.length > 0) {
          //   console.log(this.edgesForPathDisplay);
          //   let diff = elements.filter(element => this.edgesForPathDisplay.map(x => x.data.id).indexOf(element.id) === -1);
          //   console.log(diff);
          //   if (diff.length === 0) {
          //     return;
          //   } else {
          //     this.removeEdgesForPathDisplay();
          //   }
          // }

          let unique = new Set(elements.map((e) => e.type));
          if (unique.size > 0) {
            let type = '';
            if (unique.size > 1) {
              type = '*';
            } else {
              type = unique.values().next().value;
            }

            if (self.itemAnimationTimerId) {
              clearInterval(self.itemAnimationTimerId);
            }

            EventBus.$emit('TOPOLOGY_ITEM_CHANGED', type, elements);
          }
        }, 100);
      });
    },
    onUpdate(elements) {
      let self = this;

      let getNodePos = function() {
        let nodesPos = [];
        if (self.cy.nodes().length > 0) {
          self.cy.nodes().forEach((ele) => {
            nodesPos.push({
              id: ele.id(),
              pos: ele.position()
            })
          });
        }

        return nodesPos;
      }

      let createElementNode = function(node, nodeIcon) {
        let elemNode = {
          group: 'nodes',
          data: {
            id: node.id,
            name: node.name,
            type: 'node',
            label: self.labelFormat(node),
            me: node.extension.MeId,
            product: node.extension.ProductName,
            item: node
          },
          classes: (nodeIcon === undefined) ? '' : nodeIcon
        };

        return elemNode;
      }

      let createElementLink = function(link) {
        let lableSourcePort = `${/(slot=)\d+/.exec(link.srcPortId)[0].replace('slot=', '')}/${/(port=)\d+/.exec(link.srcPortId)[0].replace('port=', '')}`;
        let labelTargetPort = `${/(slot=)\d+/.exec(link.dstPortId)[0].replace('slot=', '')}/${/(port=)\d+/.exec(link.dstPortId)[0].replace('port=', '')}`;
        let elemLink = {
          group: 'edges',
          data: {
            id: link.id,
            name: link.name,
            source: link.srcNodeId,
            target: link.dstNodeId,
            type: 'link',
            label: `${lableSourcePort} / ${labelTargetPort}`, // link.name,
            labelSourcePort: (self.showPortLabel) ? lableSourcePort : '',
            labelTargetPort: (self.showPortLabel) ? labelTargetPort : '',
            sourcePort: link.srcPortId,
            targetPort: link.dstPortId,
            sourceProduct: link.srcProductName,
            targetProduct: link.dstProductName,
            bandwidth: link.bandwidth,
            maxBandwidth: link.maxBandwidth,
            item: link,
            lineWidth: (parseInt(link.bandwidth) <= 1000000) ? 0.5 : 1.5
          },
          classes: 'link'
        };

        return elemLink;
      }

      let createElementTunnel = function(tunnel) {
        console.log(tunnel);
        let elemTunnel = {
          group: 'edges',
          data: {
            id: tunnel.id,
            name: tunnel.name,
            source: tunnel.wkLsp.srcNodeId,
            target: tunnel.wkLsp.dstNodeId,
            cir: tunnel.qosInfo.cir,
            pir: tunnel.qosInfo.pir,
            wkLspName: tunnel.wkLsp.name,
            wkLspPathType: tunnel.wkLsp.pathType,
            wkLspSrcNodeId: tunnel.wkLsp.srcNodeId,
            wkLspSrcNodeName: tunnel.wkLsp.srcNodeName,
            wkLspSrcInLabel: tunnel.wkLsp.srcInLabel,
            wkLspSrcOutLabel: tunnel.wkLsp.srcOutLabel, // 오타
            wkLspDstNodeId: tunnel.wkLsp.dstNodeId,
            wkLspDstNodeName: tunnel.wkLsp.dstNodeName,
            wkLspDstInLabel: tunnel.wkLsp.dstInLabel,
            wkLspDstOutLabel: tunnel.wkLsp.dstOutLabel,
            wkLspOamMegName: tunnel.wkLsp.oamInfo.megName,
            wkLspOamMepId: tunnel.wkLsp.oamInfo.mepId,
            wkLspOamRMepId: tunnel.wkLsp.oamInfo.rMepId,
            wkLspOamCcInterval: tunnel.wkLsp.oamInfo.ccInterval,
            ptLspName: tunnel.ptLsps[0].name,
            ptLspPathType: tunnel.ptLsps[0].pathType,
            ptLspSrcNodeId: tunnel.ptLsps[0].srcNodeId,
            ptLspSrcNodeName: tunnel.ptLsps[0].srcNodeName,
            ptLspSrcInLabel: tunnel.ptLsps[0].srcInLabel,
            ptLspSrcOutLabel: tunnel.ptLsps[0].srcOutLabel, // 오타
            ptLspDstNodeId: tunnel.ptLsps[0].dstNodeId,
            ptLspDstNodeName: tunnel.ptLsps[0].dstNodeName,
            ptLspDstInLabel: tunnel.ptLsps[0].dstInLabel,
            ptLspDstOutLabel: tunnel.ptLsps[0].dstOutLabel,
            ptLspOamMegName: tunnel.ptLsps[0].oamInfo.megName,
            ptLspOamMepId: tunnel.ptLsps[0].oamInfo.mepId,
            ptLspOamRMepId: tunnel.ptLsps[0].oamInfo.rMepId,
            ptLspOamCcInterval: tunnel.ptLsps[0].oamInfo.ccInterval,
            item: tunnel
          },
          classes: 'tunnel'
        };

        return elemTunnel;
      }

      // let createElementLsp = function(lsp) {
      //   let elemLsp = {
      //     data: {
      //       id: lsp.lspNniPathInfo.srcLinkId + '@',
      //       name: lsp.name,
      //       source: lsp.lspNniPathInfo.srcLinkSrcNodeId,
      //       target: lsp.lspNniPathInfo.srcLinkDstNodeId,
      //     }
      //   };
      //   return elemLsp;
      // }

      let createElementLsps = function(type, lsp) {
        let elemLsps = [];
        if (lsp.lspNniPathInfo.hasOwnProperty('lspNniTransitInfo') === false) {
          let elemLsp = {
            data: {
              id: lsp.lspNniPathInfo.srcLinkId + '@',
              name: lsp.name,
              source: lsp.lspNniPathInfo.srcLinkSrcNodeId,
              target: lsp.lspNniPathInfo.srcLinkDstNodeId,
              linkId: lsp.lspNniPathInfo.srcLinkId,
              linkSrcNodeId: lsp.lspNniPathInfo.srcLinkSrcNodeId,
              linkDstNodeId: lsp.lspNniPathInfo.srcLinkDstNodeId,
              linkSrcPortId: lsp.lspNniPathInfo.srcLinkSrcPortId,
              linkDstPortId: lsp.lspNniPathInfo.srcLinkDstPortId,
              srcInLabel: lsp.srcInLabel,
              srcOutLabel: lsp.srcOutLabel,
              dstInLabel: lsp.dstInLabel,
              dstOutLabel: lsp.dstOutLabel,
              labelSourcePort: ` IN: ${lsp.srcInLabel} \nOUT: ${lsp.srcOutLabel}`,
              labelTargetPort: ` IN: ${lsp.dstInLabel} \nOUT: ${lsp.dstOutLabel}`,
              item: lsp
            }
          };

          elemLsps.push(elemLsp);
        } else {
          elemLsps.push({ data: { id: lsp.lspNniPathInfo.srcLinkId + '@', source: lsp.lspNniPathInfo.srcLinkSrcNodeId, target: lsp.lspNniPathInfo.srcLinkDstNodeId } });
          elemLsps.push({ data: { id: lsp.lspNniPathInfo.dstLinkId + '@', source: lsp.lspNniPathInfo.dstLinkSrcNodeId, target: lsp.lspNniPathInfo.dstLinkDstNodeId } });

          if (lsp.lspNniPathInfo.lspNniTransitInfo.length > 2) { // transit node가 2개 이상 있는 경우 링크 추가
            // lspNniTransitInfo로 loop를 구성할 수 있게 데이터 변경 요청.
            //  RequestTunnelInfo
            // wkLsp > lspNniPathInfo > lspNniTransitInfo > westLink에 연결된 lsrId 필요 (westNodeId), eastLink에 연결된 lsrId 필요 (eastNodeId)
            // ptLsps > lspNniPathInfo > lspNniTransitInfo > westLink에 연결된 lsrId 필요 (westNodeId), eastLink에 연결된 lsrId 필요 (eastNodeId)
          }
        }

        elemLsps.forEach(function(element) {
          Object.assign(element, { group: 'edges', classes: `lsp_${type}` });
        });

        return elemLsps;
      }

      let createElementsForTopologyDisplay = function(elements) {
        let eles = [];
        let nodesPos = getNodePos();
        for (let node of elements.nodes) {
          let nodeIcon = self.nodeIcons.get(node.name.slice(0, 2));
          let elemNode = createElementNode(node, nodeIcon);
          let oldNodePos = nodesPos.find((nodePos) => {
            return nodePos.id === node.id;
          });

          if (oldNodePos) {
            Object.assign(elemNode, { position: oldNodePos.pos });
          }
          eles.push(elemNode);
        }

        for (let link of elements.links) {
          let nodesById = elements.nodes.filter((node) => {
            return (node.id === link.srcNodeId) || (node.id === link.dstNodeId);
          });

          if (nodesById.length >= 2) {
            let elemLink = createElementLink(link);
            eles.push(elemLink);
          }
        }

        let tunnelsBySection = [];
        for (let tunnel of elements.tunnels) {
          let srcNodeId = tunnel.extension.SrcNodeId;
          let dstNodeId = tunnel.extension.DstNodeId;

          if (srcNodeId.indexOf('.') === -1) {
            let node = elements.nodes.find((node) => node.extension.MeId === srcNodeId);
            if (node) {
              srcNodeId = node.id;
            }
          } else {
            if (elements.nodes.find((node) => node.id === srcNodeId) === undefined) {
              console.log(`Not found ${srcNodeId}(LSRID) has node`);
              continue;
            }
          }

          if (dstNodeId.indexOf('.') === -1) {
            let node = elements.nodes.find((node) => node.extension.MeId === dstNodeId);
            if (node) {
              dstNodeId = node.id;
            }
          } else {
            if (elements.nodes.find((node) => node.id === dstNodeId) === undefined) {
              console.log(`Not found ${dstNodeId}(LSRID) has node`);
              continue;
            }
          }

          if (srcNodeId === undefined || dstNodeId === undefined) {
            console.log(`Not found ME ID (srcNodeId='${tunnel.extension.SrcNodeId} / dstNodeId=${tunnel.extension.DstNodeId})`);
            continue;
          }

          let origin = `${srcNodeId}^${dstNodeId}`;
          let reverse = `${dstNodeId}^${srcNodeId}`;
          let tunnels = tunnelsBySection.find((tunnels) => {
            return (tunnels.id === origin || tunnels.id === reverse);
          });

          if (tunnels !== undefined) {
            tunnels.childList.push(tunnel);
          } else {
            tunnelsBySection.push({
              id: origin,
              source: srcNodeId,
              target: dstNodeId,
              childList: [tunnel]
            });
          }
        }

        tunnelsBySection.forEach((tunnels, index) => {
          eles.push({
            group: 'edges',
            data: {
              id: 'tunnels' + index,
              name: '',
              type: 'tunnels',
              count: tunnels.childList.length,
              tunnels: tunnels.childList,
              source: tunnels.source,
              target: tunnels.target,
              item: tunnels
            },
            classes: 'tunnels'
          })
        });

        return eles;
      }

      let createElementsForTunnelDisplay = function(elements, tunnel) {
        let eles = [];
        let nodesPos = getNodePos();
        for (let node of elements.nodes) {
          let nodeIcon = self.nodeIcons.get(node.name.slice(0, 2));
          let elemNode = createElementNode(node, nodeIcon);
          let oldNodePos = nodesPos.find((nodePos) => {
            return nodePos.id === node.id;
          });

          if (oldNodePos) {
            Object.assign(elemNode, { position: oldNodePos.pos });
          }
          eles.push(elemNode);
        }

        let newTunnel = createElementTunnel(tunnel);
        eles.push(newTunnel);

        let lsps = createElementLsps('wk', tunnel.wkLsp);
        lsps = lsps.concat(createElementLsps('pt', tunnel.ptLsps[0]));
        lsps.forEach(function(lsp) {
          for (let link of store.state.elements.links) {
            let nodesById = store.state.elements.nodes.filter((node) => {
              return (node.id === link.srcNodeId) || (node.id === link.dstNodeId);
            });

            if (lsp.data.id.slice(0, -1) !== link.id) {
              continue;
            }

            if (nodesById.length < 2) {
              continue;
            }

            let newLink = createElementLink(link);
            eles.push(newLink);
          }
        }, this);

        eles = eles.concat(lsps);
        console.log(eles);
        return eles;
      }

      let cy = this.cy;
      if (this.flagLspDisplayMode) {
        HTTP.get('requestTunnel.json').then((response) => {
          let tunnel = response.data.content.requestTunnelInfo.response;
          let eles = createElementsForTunnelDisplay(elements, tunnel);

          cy.startBatch();
          cy.elements().remove();
          cy.add(eles);
          cy.elements().qtip({ content: () => { } });
          cy.endBatch();

          $('#topology-toolbar').css('visibility', 'hidden');
          $('#topology-randomize').css('visibility', 'hidden');
        }).catch((err) => {
          console.log(err);
        });
      } else {
        let eles = createElementsForTopologyDisplay(elements);
        cy.startBatch();
        cy.elements().remove();
        cy.add(eles);

        if (!this.initLoad) {
          cy.center(this.cy.nodes());

          let layout = cy.layout({
            name: 'cose-bilkent',
            nodeDimensionsIncludeLabels: true,
            animate: 'end',
            animationEasing: 'ease-out',
            animationDuration: 500,
            padding: 50,
            tilingPaddingHorizontal: 30,
            randomize: true
          });

          layout.run();

          $('#topology-randomize').on('click', function() { layout.run(); });
          $('#topology-toolbar').css('visibility', 'visible');
          $('#topology-randomize').css('visibility', 'visible');
          this.initLoad = true;
        }

        // register empty qtip for using qtip api
        cy.elements().qtip({ content: () => { } });
        cy.endBatch();
      }
    }
  }
}
</script>

<style>
@import '../../node_modules/cytoscape-panzoom/cytoscape.js-panzoom.css';
@import '../../node_modules/cytoscape-panzoom/font-awesome-4.0.3/css/font-awesome.min.css';
@import '../../node_modules/cytoscape-context-menus/cytoscape-context-menus.css';
@import '../../node_modules/cytoscape-navigator/cytoscape.js-navigator.css';
@import '../../node_modules/qtip2/dist/jquery.qtip.css';

#cy {
  width: 100%;
  height: 100%;
  position: relative;
  top: 0px;
  left: 0px;
  font-size: 8px;
  font-family: 'helvetica neue, helvetica, arial, sans-serif';
}

#topology-randomize {
  position: absolute;
  z-index: 1;
  top: 1px;
  right: 520px;
  visibility: hidden;
}

#topology-toolbar {
  position: absolute;
  z-index: 1;
  top: 1px;
  right: 410px;
  visibility: hidden;
}

#topology-chosen {
  position: absolute;
  z-index: 1;
  top: 1px;
  right: 1px;
}

.cytoscape-navigator {
  position: fixed;
  border: 1px solid #e5e5e5;
  background: #fff;
  width: 200px;
  height: 200px;
  bottom: 0;
  right: 0;
  overflow: hidden;
}

.btn.focus,
.btn:active.focus,
.btn.active.focus {
  outline: none;
}

.btn.focus {
  border: 1px solid #ccc;
  background: #fff;
}

.qtip {
  max-width: 1000px;
  font-size: 10px;
  font-family: 'helvetica neue, helvetica, arial, sans-serif';
}

.chosen-select-deselect {
  width: 400px;
}

.chosen-container-single .chosen-single {
  height: 29px;
  line-height: 29px;
  font-size: 12px;
}
</style>


