<template>
  <div id='app'>
    <div id='jsGrid'></div>
    <simple-modal></simple-modal>
  </div>
</template>

<script>
import 'expose-loader?$!expose-loader?jQuery!jquery'
import jsGrid from 'jsGrid'
import EventBus from './event-bus'
import simpleModal from './simpleModal'

export default {
  components: {
    simpleModal
  },
  mounted() {
    this.init();

    let self = this;
    EventBus.$on('TOPOLOGY_ITEM_CHANGED', function(type, elements) {
      self.onUpdateGrid(type, elements);
    });
  },
  data() {
    return {
      selectedRow: null,
      gridRows: []
    }
  },
  methods: {
    init() {
      var self = this;
      $('#jsGrid').jsGrid({
        width: '100%',
        height: '100%',
        filtering: true,
        editing: false,
        autoload: true,
        sorting: true,
        paging: false,
        data: [],
        controller: {
          loadData: function(filter) {
            return $.grep(self.gridRows, function(item) {
              // node
              if (filter.hasOwnProperty('Me')) {
                return (!filter.ID || item.ID.indexOf(filter.ID) > -1) &&
                  (!filter.Name || item.Name.indexOf(filter.Name) > -1) &&
                  (filter.hasOwnProperty('Me') && (!filter.Me || item.Me.indexOf(filter.Me) > -1)) &&
                  (filter.hasOwnProperty('Product') && (!filter.Product || item.Product.indexOf(filter.Product) > -1));// &&
              }

              // tunnel
              if (filter.hasOwnProperty('wkLabel')) {
                return (!filter.ID || item.ID.indexOf(filter.ID) > -1) &&
                  (!filter.Name || item.Name.indexOf(filter.Name) > -1) &&
                  (!filter.Path || item.Path.indexOf(filter.Path) > -1) &&
                  (!filter.wkLabel || item.wkLabel.indexOf(filter.wkLabel) > -1) &&
                  (!filter.PtLabel || item.PtLabel.indexOf(filter.PtLabel) > -1) &&
                  (!filter.pathType || item.pathType === filter.pathType);
              }

              // link
              if (filter.hasOwnProperty('Path')) {
                return (!filter.ID || item.ID.indexOf(filter.ID) > -1) &&
                  (!filter.Name || item.Name.indexOf(filter.Name) > -1) &&
                  (!filter.Path || item.Path.indexOf(filter.Path) > -1);
              }

              // *
              return (!filter.ID || item.ID.indexOf(filter.ID) > -1) &&
                (!filter.Name || item.Name.indexOf(filter.Name) > -1);
            });
          }
        },
        fields: this.createGridFields('node'),
        noDataContent: 'No items selected',
        rowClick: function(args) {
          if (self.selectedRow) {
            let $row = this.rowByItem(self.selectedRow);
            if ($row) {
              $row.children('.jsgrid-cell').css('background-color', '');
            }
          }

          let $row = this.rowByItem(args.item);
          $row.children('.jsgrid-cell').css('background-color', '#68B4FD');
          self.selectedRow = args.item;

          EventBus.$emit('GRID_ITEM_SELECTED', args.item);
        }
      });

      EventBus.$on('GRID_RESIZED', () => {
        $('#jsGrid').jsGrid('refresh');
        if (self.selectedRow) {
          let $row = $('#jsGrid').jsGrid('rowByItem', self.selectedRow);
          if ($row) {
            $row.children('.jsgrid-cell').css('background-color', '#68B4FD');
          }
        }
      });
    },
    createGridFields(type) {
      switch (type) {
        case 'node':
          return [
            { type: 'number', name: 'No', width: 20, filtering: false, align: 'center' },
            { type: 'text', name: 'ID', width: 50 },
            { type: 'text', name: 'Name', width: 50 },
            { type: 'text', name: 'Me', title: 'ME ID', width: 50 },
            { type: 'text', name: 'Product', title: 'Product Name', width: 100 },
            { type: 'control', editButton: false, deleteButton: false, modeSwitchButton: false, width: 20 }
          ];
        case 'link':
          return [
            { type: 'number', name: 'No', width: 20, filtering: false, align: 'center' },
            { type: 'text', name: 'ID', width: 100 },
            { type: 'text', name: 'Name', width: 200 },
            { type: 'text', name: 'Path', title: 'Source / Destination', width: 200 },
            { type: 'text', name: 'Bandwidth', width: 50, filtering: false },
            { type: 'text', name: 'MaxBw', title: 'Max Bandwidth', width: 50, filtering: false },
            { type: 'control', editButton: false, deleteButton: false, modeSwitchButton: false, width: 20 }
          ];
        case 'tunnels':
          return [
            { type: 'number', name: 'No', width: 20, filtering: false, align: 'center' },
            { type: 'text', name: 'ID', width: 200 },
            { type: 'text', name: 'Name', width: 200 },
            { type: 'text', name: 'Path', title: 'Source / Destination', width: 200 },
            { type: 'text', name: 'wkLabel', title: 'Working Label', width: 200 },
            { type: 'text', name: 'PtLabel', title: 'Protection Label', width: 200 },
            //           { type: 'text', name: 'QoS', width: 50, filtering: false },
            { type: 'select', name: 'pathType', title: 'Type', items: ['', 'Outer', 'Inner'], width: 50 },
            { type: 'control', editButton: false, deleteButton: false, modeSwitchButton: false, width: 25 }
          ]
        case '*':
          let self = this;
          return [
            { type: 'number', name: 'No', width: 20, filtering: false, align: 'center' },
            { type: 'text', name: 'ID', title: 'ID', width: 200 },
            { type: 'text', name: 'Name', title: 'Name', width: 200 },
            {
              type: 'text',
              name: 'raw',
              title: 'Raw',
              align: 'center',
              width: 30, 
              filtering: false,
              editButton: false, 
              deleteButton: false,
              modeSwitchButton: false,
              itemTemplate: function(value, item) {
                return $('<button>').addClass('btn btn-mini').append('<i class="icon-zoom-in"></i>').on('click', function() {
                  self.$modal.show('simple-modal', item);
                });
              }
            },
            { type: 'control', editButton: false, deleteButton: false, modeSwitchButton: false, width: 20 }
          ]
      }
    },
    createGridRows(type, elements) {
      let rows = [];
      switch (type) {
        case 'node':
          for (let element of elements) {
            rows.push({
              ID: element.id,
              Name: element.name,
              Me: element.me,
              Product: element.product,
              item: element
            });
          }
          break;
        case 'link':
          for (let element of elements) {
            rows.push({
              ID: element.id,
              Name: element.name,
              Path: `${element.item.srcNodeId} - ${element.item.srcPortId} / ${element.item.dstNodeId} - ${element.item.dstPortId}`,
              Bandwidth: element.bandwidth,
              MaxBw: element.maxBandwidth,
              item: element
            });
          }
          break;
        case 'tunnels':
          for (let element of elements) {
            for (let tunnel of element.tunnels) {
              rows.push({
                ID: tunnel.id,
                Name: tunnel.lspNameWk.replace('_wk', ''),
                Path: '',
                wkLabel: '',
                ptLabel: '',
                //                Qos: '',
                pathType: (tunnel.pathType === 'Outer') ? 1 : 2,
                item: tunnel
              });
            }
          }
          break;
        case '*':
          for (let element of elements) {
            if (element.type === 'tunnels') {
              for (let tunnel of element.tunnels) {
                rows.push({
                  ID: tunnel.id,
                  Name: tunnel.lspNameWk.replace('_wk', ''),
                  raw: tunnel,
                  item: tunnel
                });
              }
            } else {
              rows.push({
                ID: element.id,
                Name: element.name,
                raw: element.item,
                item: element
              });
            }
          };
          break;
      }

      rows.forEach(function(element, index) {
        Object.assign(element, { No: (index + 1) });
      });

      return rows;
    },
    onUpdateGrid(type, elements) {
      let grid = $('#jsGrid');
      // grid.jsGrid('option', 'autoload', false);
      grid.jsGrid('option', 'autoload', false);
      grid.jsGrid('option', 'fields', this.createGridFields(type));
      this.gridRows = this.createGridRows(type, elements);
      grid.jsGrid('option', 'data', this.gridRows);
      grid.jsGrid('refresh');
    }
  }
}
</script>

<style>
@import '../../node_modules/jsgrid/dist/jsgrid.min.css';
@import '../../node_modules/jsgrid/dist/jsgrid-theme.min.css';

#jsGrid {
  font-size: 11px;
}

.jsgrid-cell {
  padding: 1px 3px 1px 3px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.jsgrid-header-cell {
  padding: 0.5em 0.5em;
  text-align: center;
}

.selected-row {
  background-color: #F7B64B;
}
</style>


