<template>
  <modal name="simple-modal" :adaptive="true" @before-open="beforeOpen"> <!-- :resizable="true" :draggable="true" -->
    <div class="box-title ">
      <h5><b>Detail infomation</b></h5>
    </div>
    <div class="box-messages">
      <pre> {{ message }} </pre>
    </div>
  </modal>
</template>

<script>
export default {
  name: 'simpleModal',
  data() {
    return {
      title: '',
      message: ''
    }
  },
  methods: {
    beforeOpen(event) {
      this.title = event.params.ID;
      this.message = JSON.stringify(event.params.raw, undefined, 2);
    }
  }
}
</script>

<style scope>
.box-title {
  text-align: center;
  border-bottom: solid 1px #e0e0e0;
}

.box-messages {
  padding: 10px;
  text-align: left;
}
</style>


